import React, { useEffect, useState } from "react";

import Stories from "react-insta-stories";
import axios from "axios";

function Slidetime() {
  const [terdMovie, setterdMovie] = useState([]);
  const [terdMoviexx, setterdMoviexx] = useState('');

  //---------------------------------componat will mount onchange----------------------------
  useEffect(() => {
    getAllCateTrendingFromApi();
    getAllCateTrendingFromApixx();
  }, []);

  // --------------------------------- function to get all media type from api      ----------------------------

  async function getAllCateTrendingFromApi() {
    let { data } = await axios.get(
      `https://api.themoviedb.org/3/trending/movie/week?api_key=16e5f62cf801b1d8b2bda7b0c2a81953`
    );
    setterdMovie(data.results);
  }

  async function getAllCateTrendingFromApixx() {
    let { data } = await axios.get(
      `https://laravel.hekaia.net/api/get-all-stories`
    );
    setterdMoviexx(data);
    await console.log(data);
  }

  // for (let index = 0; index < terdMovie.length; index++) {
  //   const element = array[index];

  // }
  //   const stories = terdMovie.map((item, index) =>
  //   {
  //     url: "https://image.tmdb.org/t/p/w500/" + item.poster_path
  // }  )

  // if (terdMovie) {
  //   let x =  terdMovie.map(
  //     (item, index) => "https://image.tmdb.org/t/p/w500/" + item.poster_path
  //   );

  // }
  let xx = [];
  terdMovie.map((item, index) => {
    const x = "https://image.tmdb.org/t/p/w500/" + item.poster_path;
    xx.push(x);
  });

  const stories = [
    xx[0],

    // {x},
    //   {
    //     url: "https://mdbcdn.b-cdn.net/img/Photos/Vertical/mountain1.webp",
    //     header: {},
    //   },
    //   {
    //     url: "https://mdbcdn.b-cdn.net/img/Photos/Vertical/mountain3.webp",
    //     header: {},
    //   },
    //   {
    //     url: "https://mdbcdn.b-cdn.net/img/Photos/Vertical/mountain2.webp",
    //     header: {},
    //   },
    // "https://mdbcdn.b-cdn.net/img/Photos/Vertical/mountain2.webp",
    // "https://mdbcdn.b-cdn.net/img/Photos/Vertical/mountain2.webp",
  ];

  const [currentId, setCurrentId] = useState(0);

  return (
    <>
      {/* {terdMovie && terdMovie.map((item, index) => <Stories
        key={index}
        currentIndex={currentId}
        loop
        keyboardNavigation={true}
        stories={stories}
        defaultInterval={3500}
        width={432}
        height={768}
        onStoryEnd={(s, st) => {
          console.log("story ended", s, st);
          setCurrentId((currentId) => currentId + 1);
        }}
        onAllStoriesEnd={(s, st) => {
          console.log("all stories ended", s, st);
          setCurrentId((currentId) => 0);
        }}
      />)} */}
      <Stories
        currentIndex={currentId}
        loop
        keyboardNavigation={true}
        stories={stories}
        defaultInterval={3500}
        width={432}
        height={768}
        onStoryEnd={(s, st) => {
          console.log("story ended", s, st);
          setCurrentId((currentId) => currentId + 1);
        }}
        onAllStoriesEnd={(s, st) => {
          console.log("all stories ended", s, st);
          setCurrentId((currentId) => 0);
        }}
      />
      {terdMoviexx && <h1 className="bg-danger">{terdMoviexx[0].created_at}</h1>}
    </>
  );
}

export default Slidetime;
